.poptreatments{
    height: auto;
    padding: rem(20px 20px);
    font-family: 'Roboto';
    font-size: 20px;
  
  &__subtitle {
    font-weight: 600;
    height: 60px;
    text-align: center;
    line-height: 3.5rem;
  }
  .grid {
    height: auto;
    width: auto;
    display: grid;
    grid-template-columns: repeat(auto-fill, rem(minmax(130px, 1fr)));
    background-color: #fff;
    // grid-template-rows: minmax(200px 300px);
    grid-gap: 10px;
    
    .item {
      text-align: center;
      font-size: rem(16px);
      font-family: 'Roboto';
      background-color: #fff;
      bottom: 0;
    } 
    &--item1{
     display: block;
    }
}
}
